.App{
  height: 100%;
  width: 100%;
  text-align: center;
  overflow: hidden;
  display: grid;
grid-template-columns: repeat(5, 1fr);
grid-template-rows: 0.8fr repeat(3, 1fr) 0.4fr;
grid-column-gap: 0px;
grid-row-gap: 0px;

}

 .App .Header{
  height: 100%;
  width: 100%;
  display: flex;
  justify-content:space-between;
  align-items: center;
  box-sizing: border-box;
  background-color: #8f7d56;
  grid-area: 1 / 1 / 2 / 6;

}



.App .Main{
  width: 100%;
  height: 100%;
  overflow-y:scroll;
  align-items: flex-start;
  grid-area: 2 / 1 / 5 / 6;
  
}


.App .Footer{
  width: 100%;
  height: 100%;
  font-size: large;
  color: white;
  background-color: #ffffff28;
  display: flex;
  align-items: center;
  justify-content: center;
  bottom: 0;
  grid-area: 5 / 1 / 6 / 6;
}



#errors {
  background-color: #ffffff28;
  color: rgb(0, 0, 0);
  border: 1px solid white;
  text-transform: uppercase;  
}

form {
  background-color: #ffffff28;
  padding: 10px;
  width: 500px;
  border: 1px solid white;
}


input {
  background-color: white;
  border: none;
  width: 40%;
  height: 30px;
  text-align: center;
}

select{
  background-color: white;
  border: none;
  width: 40%;
  height: 30px;
  text-align: center;
}

button {
  border: none;
  padding: 10px;
  background-color: white;

}


tr {
  background-color: rgba(255, 255, 255, 0.062);
  border-bottom: solid 1px white;
}



.myTable {
  height: 50%;
}

th, td {
  padding: 15px;

}

table > #id {
  color: white;
  font-weight: bold;
}


thead {
  color: black;

}

th > .CustomLink {
  color: black;
  border: 2px solid black;
}